.root {
  position: relative;
  border-radius: 50%;
  display: block;
  height: 24px;
  overflow: hidden;
  width: 24px;
  transition: 0.5s all ease;
  input {
    display: none;
  }
  &:hover {
    cursor: pointer;
  }
  &:before {
    content: "";
    display: block;
    position: absolute;
  }
  &.light:before {
    animation-duration: 0.5s;
    animation-name: sun;
    background-color: var(--theme-icon-color);
    border-radius: 50%;
    box-shadow: 10px 0 0 -3.5px var(--theme-icon-color),
    -10px 0 0 -3.5px var(--theme-icon-color),
    0 -10px 0 -3.5px var(--theme-icon-color),
    0 10px 0 -3.5px var(--theme-icon-color),
    7px -7px 0 -3.5px var(--theme-icon-color),
    7px 7px 0 -3.5px var(--theme-icon-color),
    -7px 7px 0 -3.5px var(--theme-icon-color),
    -7px -7px 0 -3.5px var(--theme-icon-color);
    height: 10px;
    left: 7px;
    top: 7px;
    width: 10px;
    &:hover {
      background-color: var(--background-color);
      box-shadow: 10px 0 0 -3.5px var(--background-color),
      -10px 0 0 -3.5px var(--background-color),
      0 -10px 0 -3.5px var(--background-color),
      0 10px 0 -3.5px var(--background-color),
      7px -7px 0 -3.5px var(--background-color),
      7px 7px 0 -3.5px var(--background-color),
      -7px 7px 0 -3.5px var(--background-color),
      -7px -7px 0 -3.5px var(--background-color);
    }
  }
  &.dark {
    &:before {
      animation-duration: .5s;
      animation-name: moon;
      background-color: var(--theme-icon-color);
      border-radius: 50%;
      height: 20px;
      left: 2px;
      top: 2px;
      width: 20px;
      z-index: 1;
      &:hover {
        background-color: var(--background-color);
      }
    }
    &:after {
      animation-duration: .5s;
      animation-name: moon-shadow;
      background: var(--background-color);
      border-radius: 50%;
      content: "";
      display: block;
      height: 18px;
      position: absolute;
      right: -2px;
      top: -2px;
      width: 18px;
      z-index: 2;
    }
  }
}

@keyframes sun {
  from {
    background-color: var(--background-color);
    box-shadow: 0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color),
    0 0 0 -5px var(--background-color);
  }
  to {
    background-color: var(--theme-icon-color);
    box-shadow: 10px 0 0 -3.5px var(--theme-icon-color),
    -10px 0 0 -3.5px var(--theme-icon-color),
    0 -10px 0 -3.5px var(--theme-icon-color),
    0 10px 0 -3.5px var(--theme-icon-color),
    7px -7px 0 -3.5px var(--theme-icon-color),
    7px 7px 0 -3.5px var(--theme-icon-color),
    -7px 7px 0 -3.5px var(--theme-icon-color),
    -7px -7px 0 -3.5px var(--theme-icon-color);
  }
}

@keyframes moon {
  from {
    height: 0;
    left: 12px;
    top: 12px;
    width: 0;
  }
  to {
    height: 20px;
    left: 2px;
    top: 2px;
    width: 20px;
  }
}

@keyframes moon-shadow {
  from {
    background-color: var(--background-color);
    height: 0;
    right: 7px;
    top: 7px;
    width: 0;
  }
  to {
    background-color: var(--background-color);
    height: 18px;
    right: -2px;
    top: -2px;
    width: 18px;
  }
}